import { Component, OnInit, ElementRef, ViewChild, AfterViewInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
declare var $: any;

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit, AfterViewInit {

  constructor( private route: ActivatedRoute ) {}

  title = 'portfolio';

  scripts = [
    './assets/lib/js/popper.min.js',
    './assets/lib/js/bootstrap.min.js',
    './assets/lib/js/isotope.js',
    './assets/lib/js/imagesloaded.pkgd.min.js',
    './assets/lib/js/dynamics.min.js',
    './assets/lib/js/classie.js',
    './assets/lib/js/isolayer.js',
    './assets/lib/js/fancybox.min.js',
    './assets/lib/js/swiper.min.js',
    './assets/lib/js/perspective.js',
    './assets/lib/js/anime.min.js',
    './assets/lib/js/wow.min.js',
    './assets/lib/js/odometer.min.js',
    './assets/lib/js/readmore.min.js',
    './assets/lib/js/scripts.js'
  ]

  ngOnInit() {
    this.scripts.forEach((script) => {
      $.getScript(script);
    });
  }

  ngAfterViewInit(): void {
    this.route.fragment.subscribe(fragment => {
       if (fragment) {
         document.querySelector('#' + fragment).scrollIntoView({ behavior: 'smooth' });
      }
    });
  }
}
